<template>
	<div class="">
		<div class="table_content_body">
			<div class="table_content_body_left">
				<div v-for="item in leftNav" class="left_nav cursor">
					<router-link :to="{name: item.component}">
						<i class="icon iconfont" :class="'icon-'+item.icon"></i>
						<span>{{ item.text }}</span>
					</router-link>
				</div>
			</div>
			<div class="table_content_body_right">
				<router-view></router-view>
			</div>
			<div class="clear"> </div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getRouterListApi } from '../../config/api'
	
	export default {
		name: 'fundDataHome',
		data(){
			return {
				rootid1: '',
				rootid2: '',
				leftNav: [
//					{
//						component: "directInvestProject",
//						icon: 'zhitouxiangmu1',
//						text: '直投项目'
//					},
//					{
//						component: "subFundProject",
//						icon: 'zijijinxiangmu',
//						text: '子基金项目'
//					},
				]
			}
		},
		created(){
			this.getRouterList()
		},
		methods: {
			// 获取导航路由数据
			getRouterList(){
				getRouterListApi({
					specMenuId: 16
				}).then(res => {
					// console.log(res);
					if(res.data.msgNo == 1) {
						this.leftNav = res.data.payload;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
		}
	}
</script>

<style>

</style>


